.main-image {
	max-width: 100%;
	max-height: 100%;
	object-fit: cover;
	object-position: center;
}

h1,
h2 {
	margin: 0.7em 0;
	text-transform: uppercase;
}

.main-info {
	// position: relative;
	top: 30px;
	margin: 2em 0;

	@media screen and (max-width: 760px) {
		flex-direction: column;
		align-items: center;
		text-align: center;
		height: 80vh;
	}
	.heading {
		text-transform: uppercase;
		margin-bottom: 1em;
	}
}

.map {
	position: relative;
	top: 30px;
}
